  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1[data-v-3b884edb], h2[data-v-3b884edb] {
  font-weight: normal;
}
ul[data-v-3b884edb] {
  list-style-type: none;
  padding: 0;
}
li[data-v-3b884edb] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-3b884edb] {
  color: #42b983;
}

.wrap {
	text-align: center;
}
input {
	display: block;
	width: 250px;
	height: 40px;
	line-height: 40px;
	margin: 0 auto;
	margin-bottom: 10px;
	outline: none;
	border: 1px solid #888;
	padding: 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
